@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul.events-list {
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3em;
    margin-bottom: 1em;
}

ul.events-list li {
    display: flex;
    flex-direction: column;
}

ul.events-list li header {
    width: 100%;
    height: 15vh;
    background-size: contain;
    background-repeat: no-repeat;
}

ul.events-list li strong{
    margin-top: 1em;
    font-size: 1.1em;
    color: white;
}

div.filter-panel{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: -2em; 
    margin-bottom: 1em; 
}

div.filter-panel button{
    width: auto;
}

ul.events-list header button {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

ul.notifications {
    list-style: none;
    padding-bottom: inherit;
}

/* For smaller devices, let's change the grid to have single column */
@media (max-width:768px){
    ul.events-list{
        grid-template-columns: 1fr;
    }
}
label#thumbnail {
    border: 1px;
    cursor: pointer;
    display: flex;
    align-items: center;
    height: 15vh;
}

label#thumbnail input {
    display: none;
}

label#thumbnail.has-thumbnail {
    border: 0;
    background-size: contain;
    background-repeat: no-repeat;
}

label#thumbnail.has-thumbnail img {
    display: none;
}
.events {
    list-style: none;
    display: flex;
    flex-direction: column;
}

.events li {
    padding: 1em;
    margin: 1rem;
    border-radius: 0.2em;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.events div {
    margin-top: 0.5rem;
}

.events-details span {
    margin: 0.5rem;
}

.Approved {
    color: green;
}

.Rejected {
    color: red;
}

.Pending {
    color: blue;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html,
body,
#root {
    max-width: 100%;
    background-color: #47596e;
}


body,
input,
button {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-size: 1rem;
}

::-webkit-input-placeholder {
    font-size: 0.9em;
}

:-ms-input-placeholder {
    font-size: 0.9em;
}

::-ms-input-placeholder {
    font-size: 0.9em;
}

::placeholder {
    font-size: 0.9em;
}

.container {
    margin: 2% auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container h1 {
    color: #ffffff;
    font-size: 2.5rem;
}

.container p {
    color: #ffffff;
    font-size: 1rem;
}

.content input {
    width: 100%;
}

.container label {
    color: #ffffff;
    font-size: 1rem;
}

.content {
    display: flex;
    flex-direction: column;
    width: 80%;
    background: #908F9B;
    margin-top: 5%;
    border-radius: 0.5em;
    padding: 5%;
}

.content h2 {
    color: #ffffff;
}

.content > p {
    font-size: 1rem;
    color: #ffffff;
}

.content button {
    border-radius: 0.2em;
    border: 0;
    font-size: 1em;
    font-weight: 700;
    cursor: pointer;
    height: 2.5em;
    width: 100%;
}


.content button.submit-btn {
    background: #f05a5b;
}

.content button.submit-btn:hover {
    background: #e14f50;
}

.content button.secondary-btn {
    background: #337ab7;
}

.content button.secondary-btn:hover {
    background: #286090;
}

